

* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #e0ffff;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  width: 100%;
  background-color: #a282ee;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: fixed;
}

.input-container {
  position: relative;
}

input {
  width: 250px;
  border: rgba(0, 0, 0, 0);
  border-radius: 7px;
  padding: 5px;
}

input:focus {
  outline: rgba(0, 0, 0, 0);
}

.btn-submit {
  cursor: pointer;
  border: rgba(0, 0, 0, 0);
  border-radius: 7px;
  padding: 5px;
  position: absolute;
  top: 1px;
  right: 0;
}

.btn-submit:hover {
  background-color: #e6dccf;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  padding-top: 60px;
  display: flex;
}

.photo-card {
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

img {
  width: 250px;
  height: 180px;
  object-fit: cover;
}

svg {
  display: block;
}

.info {
  justify-content: space-around;
  font-size: 13px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #1e1eaf;
  border: rgba(0, 0, 0, 0);
  border-radius: 7px;
  margin: 10px auto;
  padding: 7px 12px;
  font-size: 16px;
  font-weight: 700;
  display: block;
}

.load-more:hover {
  background-color: #0a0a85;
}

.is-hidden {
  display: none;
}

